import React, { ReactNode } from 'react';

export interface TabPanelProps {
    id: string;
    children: ReactNode;
}

const TabPanel = ({ id, children }: TabPanelProps) => {
    return (
        <div role="tabpanel" id={`${id}-panel`} aria-labelledby={id}>
            {children}
        </div>
    );
};

export default TabPanel;
